[class^="col"] {
  padding: 0;
}

.row {
  padding: 0;
  margin: 0;
}

.container-fluid {
  padding: 0;
  margin: 0;
  overflow: hidden;
  height: 100vh;
}

#app-navbar-collapse {
  padding: 0;
}

.menu-raq {
  min-height: 100%;
  min-height: 100vh;
  background-color: #3E505C;
}

.circle {
  display: block;
  margin: 15px auto;
  width: 60px;
  height: 60px;
  background: #fff;
  border-radius: 50%;
}

.nav > li > a:hover, .nav > li > a:focus {
  text-decoration: none;
  background-color: transparent;
}

.header-raq {
  height: 150px;
}

.dropdown-menu {
  background: #3E505C;
}

.panel-body {
  padding: 0;
}

a:focus, a:active {
  background: transparent;
}

.nav .open a:focus {
  background: transparent;
}

.navbar-raq {
  width: 100%;
}

.navbar-raq li a {
  border-left: 3px solid transparent;
}

.navbar-raq li.active a {
  border-left: 3px solid #EB6555;
}

.navbar-raq li a {
  width: 100%;
  color: #fff;
}

.navbar-raq li a:hover {
  color: #3097D1;
}

table tr > th, table tr > td {
  padding-left: 15px !important;
}

.form-group {
  margin-top: 0;
}

.text-align-center {
  text-align: center;
}

.text-align-center .btn {
  display: inline-block;
}
